.App{background:#FFFFFF;padding:30px 50px;max-width:1000px;margin:auto;box-sizing:border-box}.row-flex{display:flex;align-items:flex-end;margin-bottom:40px}.col-flex{display:flex;flex-direction:column}.FilterDevices{max-width:320px;margin:0 20px 20px 0}.Unit{margin-bottom:30px}

.FilterDevices{font-size:16px;font-style:normal;font-weight:400;line-height:24px;letter-spacing:0.16px;text-align:left;color:#8C8C8C}.FilterDevices p{margin:0 0 1em 0}.FilterDevices form{display:flex;justify-content:space-between;align-items:center}.FilterDevices input[type="submit"]{background-color:#353535;color:white;border-radius:3px;font-style:normal;font-weight:500;font-size:14px;line-height:24px;width:90px;height:40px;box-shadow:none;border:none}.FilterDevices input[type="text"]{background:#FFFFFF;border:1px solid #E9E9E9;width:210px;height:30px;font-style:normal;font-weight:normal;font-size:16px;line-height:24px;margin-right:18px;padding:0 10px 0 12px}

.Unit{font-style:normal;font-weight:500;font-size:20px;line-height:24px;display:flex;align-items:flex-start}.Unit p{margin:0;width:225px}.Unit img{margin-right:20px}

.DeviceInfo{list-style-type:none;padding:20px;border-radius:5px;background-color:#f7f7f7;font-style:normal;font-weight:500;font-size:14px;line-height:17px}.DeviceInfo .info-name{color:#8C8C8C}.DeviceInfo .info-description{text-decoration:none}.DeviceInfo li{margin-bottom:.5em}

.ReportFilters{display:inline-block}.ReportFilters select{background:#FFFFFF;border:1px solid #E9E9E9;font-style:normal;font-weight:normal;font-size:16px;line-height:24px;width:220px;height:30px;padding-left:12px}.ReportFilters input{font-style:normal;font-weight:normal;font-size:14px;line-height:20px;margin-right:12px;vertical-align:middle}.ReportFilters button{font-style:normal;font-weight:500;font-size:14px;line-height:24px;background:#4990E2;border-radius:3px;border:none;color:white;width:180px;height:40px;margin:50px 0 10px}.ReportFilters label{vertical-align:middle}

.Report{font-size:13px;line-height:15px;background:#FFFFFF;border:1px dashed #939DA6;box-sizing:border-box;box-shadow:0px 0px 40px rgba(0,0,0,0.15)}.Report .container{padding:40px}.Report p{font-size:16px;font-weight:bold;line-height:19px;color:#353535}.Report table{text-align:left;border-collapse:collapse}.Report tr:not(:first-child):not(:last-child){border-bottom:1px solid #E9E9E9}.Report th{color:#939DA6;padding:20px 40px 0 0;font-weight:normal}.Report td{vertical-align:baseline;padding:20px 40px 20px 0}.Report td:first-child{font-size:16px;line-height:19px}.Report img{margin-left:20px;vertical-align:middle}

